*{
  padding: 0;
  margin: 0;
  background-color: black;
  color: white;
}

.left{
  background-color: blue;
  position: absolute;
  width: 7.5%;
  height: 100%;
}

#lightbox{
  position: fixed;
  z-index: 999;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background-color: black;
  display: none;
  justify-content: center;
  align-content: center;
}